* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.loader {
  color: #4b0a0a;
  background: #de3500;
  width: 48px;
  height: 48px;
  animation: 1s linear infinite alternate layers1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotateX(65deg)rotate(45deg);
}

.loader:after {
  content: "";
  background: #da5698b3;
  animation: 1s linear infinite alternate layerTr;
  position: absolute;
  inset: 0;
}

@keyframes layers1 {
  0% {
    box-shadow: 0 0;
  }

  90%, 100% {
    box-shadow: 20px 20px 0 -4px;
  }
}

@keyframes layerTr {
  0% {
    transform: translate(0)scale(1);
  }

  100% {
    transform: translate(-25px, -25px)scale(1);
  }
}

